.drop-down-menu {
    position: absolute;
    overflow: visible;
    bottom: 2rem;
    width: 12rem;
    height: 100px;
    z-index: 10;
    background-color: blue;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: left;

    &:hover {
        background-color: unset;
    }
}

